/* eslint-disable max-len */
import React from 'react';
import { Helmet } from 'react-helmet';

const PageHelmet = () => {
  return (
    <Helmet>
      <link
        href="https://fonts.googleapis.com/css?family=Montserrat:400,700,200"
        rel="stylesheet"
      />
      <link
        href="https://maxcdn.bootstrapcdn.com/font-awesome/latest/css/font-awesome.min.css"
        rel="stylesheet"
      />
      <title>Eurka Environnement</title>
      <meta
        name="description"
        content="Eurka Environnement est une firme de gnie-conseil spcialise dans la gestion de leau et la conception de systmes de traitement."
      />
      <meta name="keywords" content="Traitement de leau, Gestion de leau, Tests de sol, Caractrisation environnementale, Phase 1, Phase 2, Gestion des matires rsiduelles, Expertise lgal, Gnie des eaux, Gnie environnemental, Certificat dautorisation, MELCC, Article 22, Article 32" />
      
      <meta property="og:title" content="Eurka Environnement"/>
      <meta property="og:description" content="Eurka Environnement est une firme de gnie-conseil spcialise dans la gestion de leau et la conception de systmes de traitement."/>
      <meta property="og:url" content="https://eurekaenvironnement.com" />
      <meta property="og:type" content="website" />
    </Helmet>
  );
};

export default PageHelmet;
